import Vue from 'vue'
import Vuelidate from 'vuelidate'
import App from './App.vue'
import './registerServiceWorker'
import dateFilter from './filters/date.filter.js'
import router from './router'
import store from './store'
import 'materialize-css/dist/js/materialize.min.js'

import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

Vue.config.productionTip = false
Vue.use(Vuelidate)
Vue.filter('date', dateFilter)

firebase.initializeApp({
  apiKey: 'AIzaSyClrK5etObgImbjWc0FPt5YuU1egMeBHmo',
  authDomain: 'new-crm-a0460.firebaseapp.com',
  projectId: 'new-crm-a0460',
  storageBucket: 'new-crm-a0460.appspot.com',
  messagingSenderId: '105226787063',
  appId: '1:105226787063:web:6866802792a7e1ed94dbf2'
})

let app

firebase.auth().onAuthStateChanged(() => {
  if (!app) {
    app = new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }
})
